export default {
	data() {
		return {
			drawerShow: false,
			phone: uni.getStorageSync('phone'),
			allcount: '0',
			k_count: '0',
			userList: []
		}
	},
	onLoad() {
		uni.request({
			url: this.$bashUrl + '/index.php/home/Api/t_lists/',
			method: "post",
			data: {
				phone: this.phone
			},
			success: res => {
				if (res.data) {
					for(let i in res.data){
						if(typeof res.data[i] == 'object'){
							this.userList.push(res.data[i])
						}
					}
					this.allcount = res.data.allmoney
					this.k_count = res.data.k_money
					
				}
			}
		})
	},
	methods: {
		toLog() {
			uni.navigateTo({
				url: "../tixianLog/tixianLog"
			})
		},

		toMoney(count, type) {
			uni.navigateTo({
				url: "../withdraw/withdraw?k_cash=" + count + "&type=" + type
			})
		},
		toyinhangka() {
			uni.navigateTo({
				url: "../editor/editor"
			})
		},
		show() {
			this.drawerShow = true;
		},
		//取消
		cancel() {
			if (this.drawerShow) {

				console.log('您点击了取消按钮')
				this.drawerShow = false;
			}
		},
	}
}
